$color: #00cc99;
$primary: #AA0601;
$secondary: #650300;
$tertiary: #aa06010d;
$black:#000000;
$gray-1:#212529;
$gray-2:#707070;
$gray-3:#DEE2E6;

@import "reset";
@import "utility";

@import "color";


* {
  box-sizing: border-box;
  outline: 1px solid orange;
}
body{
  line-height: 1.5;
  font-family: 'Roboto', sans-serif,"微軟正黑體","Microsoft JhengHei";
  font-weight:500;
  font-size:16px;
}
h1, h2, h3, h4, h5{
  line-height: 1.2;
}
img{
  display: block;
}
.container {
  max-width: 1200px;
}
.container-xl {
  max-width: 1540px;
}
.container-md {
  max-width: 792px;
}

.header{
  height: 840px;
  background-image: url("../images/68.png");
  background-position: center center;
  background-size: cover;
}
.card{
  width: 282px;
  height: 289px;
}
.special-img{
  flex-direction: row-reverse;
}
.box{
  width: 100px;
  height: 100px;
}
.icon-box{
  width: 48px;
  height: 48px;
}
.icon-box-sm{
  width: 24px;
  height: 24px;
  img{
    width:100%;
  }
}
.phone-box{
  width: 23px;
  height: 23px;
  img{
    width:100%;
  }
}
.mail-box{
  width: 32px;
  height: 32px;
  img{
    width:100%;
  }
}
.classic-img{
  max-width: 792px;
  width:100%;
  height: 100%;
  height: 320px;
}
.img-class-1{
  background-image: url("../images/8.png");
 
}
.img-class-2{
  background-image: url("../images/15 2.png");
  &:hover{
    background-image: url("../images/15.png");

  }
}
.img-class-3{
  background-image: url("../images/28.png");

}
